@import "style/mixins.module";

.root {
    padding: 100px 0;

    text-align: center;
}

.row {
    max-width: 300px;
    margin: 0 auto;

    @include respond-to("laptop") {
        max-width: 100%;
    }
}

.error {
    margin-bottom: 16px;

    font-size: 96px;
    line-height: 124px;
    font-weight: 300;

    @include color("gray", "-2");

    @include respond-to("tablet") {
        font-size: 142px;
        line-height: 170px;
    }

    @include respond-to("laptop") {
        font-size: 240px;
        line-height: 280px;
    }
}

.title {
    margin-bottom: 8px;

    @include respond-to("laptop") {
        margin-bottom: 16px;
    }
}

.subtitle {
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}
