@import "style/mixins.module.scss";

$blue: #057ee3;
$red: #eb5e5b;
$deepRed: #e64440;
$mediumRed: #d8403c;
$darkGray: #424e58;
$white: #fff;
$lightGray: #eef3f4;
$bgGray: #e2e2df;
$cadetBlue: #abb4bb;
$outlineLight: rgba(66, 78, 88, 0.1);
$outlineGray: rgba(238, 243, 244, 0.4);

.root {
    position: relative;

    display: inline-block;
    width: fit-content;
    min-width: 120px;
    font-family: inherit;
    font-weight: 500;
    text-align: center;
    white-space: nowrap;
    color: $white;
    font-size: 17px;
    line-height: 24px;

    border: none;
    border-radius: 2px;
    cursor: pointer;
    outline-offset: 0;
    transition: background-color 0.3s;

    &--max-width {
        width: 100%;
    }
}

.root-with-icon {
    display: flex;
    align-items: center;
}

.container {
    pointer-events: none;
}

.container_hidden,
.icon_hidden {
    opacity: 0;
}

.size_small {
    padding: 8px 16px;
}

.size_medium {
    padding: 16px 20px;
}

.icon {
    width: 24px;
    height: 24px;
    margin-right: 12px;
    display: block;
}

.icon-loading {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.theme_red {
    background-color: $red;

    &:hover:not(:disabled) {
        background-color: $deepRed;
    }

    &:active {
        background-color: $mediumRed;
    }

    &:focus {
        outline: 4px solid $lightGray;
    }

    &_disabled {
        color: $white;
        cursor: default;
        background-color: $bgGray;
    }
}

.theme_bordered-transparent {
    background-color: transparent;
    outline: 1px solid $outlineLight;
    color: $darkGray;

    &:hover:not(:disabled) {
        background-color: $outlineGray;
    }

    &:active {
        background-color: $lightGray;
    }

    &:focus {
        outline: 4px solid $lightGray;
    }

    &_disabled {
        cursor: default;
        color: $cadetBlue;

        &:hover {
            background-color: $bgGray;
        }
    }
}

.theme_transparent {
    color: $blue;
    background-color: transparent;

    &:hover {
        background-color: $outlineGray;
    }

    &:focus {
        outline: 4px solid $lightGray;
    }

    &:active {
        background-color: $lightGray;
    }

    &_disabled {
        color: $cadetBlue;
    }
}

.theme_light {
    background-color: get_color("gray", "0");
    @include color("primary", "red");
}

.theme_ukraine {
    background-color: #ef8463;
    text-transform: uppercase;
    font-weight: 700;
}

.theme_germany {
    background-color: #f0d64c;
    @include color("gray", "-7");
    font-weight: 700;
}

.root_version-germany {
    border-radius: 100px;
    background-color: #db688c;
    font-family: Inter, sans-serif;
    font-weight: 400;

    &:hover:not(:disabled) {
        background-color: #902845;
    }
}
