html {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}

a {
    color: #424e58;
    text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
p {
    margin: 0;
}

body {
    position: relative;
    height: 100vh;
    margin: 0;
    bottom: 0;
    min-width: 320px;
    background: #fff;
    color: #424e58;

    font-family: "Inter", sans-serif;
    font-size: 16px;
    line-height: 1.5;

    touch-action: pan-y;
}

h1,
h2,
h3,
h4,
h5 {
    line-height: 1;
}

h2 {
    margin-bottom: 16px;
}

h3 {
    margin-bottom: 16px;
}

p {
    margin-bottom: 14px;
}
