/* Source Serif Pro 700 */
@font-face {
    font-family: "Source Serif Pro";
    font-style: normal;
    font-weight: 700;
    src: local("Source Serif Pr"), 
        url("/special/fonts/SourceSerifPro/source-serif-pro-bold.woff2") format("woff2"),
        url("/special/fonts/SourceSerifPro/source-serif-pro-bold.woff") format("woff");
}

@font-face {
    font-family: "Inter";
    font-style: normal;
    font-weight: 400;
    src: local("Inter"),
        url("/special/fonts/Inter/inter-regular.woff2") format("woff2"),
        url("/special/fonts/Inter/inter-regular.woff") format("woff");
}

@font-face {
    font-family: "Inter";
    font-style: normal;
    font-weight: 600;
    src: local("Inter"),
        url("/special/fonts/Inter/inter-semi-bold.woff2") format("woff2"),
        url("/special/fonts/Inter/inter-semi-bold.woff") format("woff");
}