@import "style/mixins.module.scss";

.root {
    font-family: Inter, sans-serif;
    font-weight: 700;
}

.type {
    &_xl {
        font-size: 36px;
        line-height: 40px;

        @include respond-to("laptop") {
            font-size: 56px;
            line-height: 64px;
        }
    }

    &_m {
        font-size: 22px;
        line-height: 28px;

        @include respond-to("laptop") {
            font-size: 28px;
            line-height: 32px;
        }
    }

    &_s {
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }
}

.theme {
    &_default {
        @include color("gray", "-4");
    }

    &_red {
        @include color("primary", "red");
    }

    &_white {
        @include color("gray", "0");
    }

    &_black {
        @include color("gray", "-5");
    }
}
