@import "style/mixins.module";

.container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    min-height: 100vh;
    padding-top: 24px;
    padding-bottom: 24px;
}

.logo {
    position: absolute;
    width: 140px;
    height: 28px;
    top: 24px;

    @include respond-to("laptop") {
        width: 160px;
        height: 32px;
    }

    svg {
        width: 100%;
        height: 100%;
    }
}

.title {
    margin-bottom: 8px;

    @include respond-to("laptop") {
        margin-bottom: 16px;
    }
}

.subtitle {
    margin-bottom: 80px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        max-width: 480px;
        margin: 0 auto 80px;
        font-size: 22px;
        line-height: 32px;
    }
}

.button {
    margin: 0 auto;
}
